::-webkit-scrollbar{
  display: none;
}

body{
  width: 100%;
  height: 100vh;
background: #313b45;
background-attachment: fixed;
color: white;
}

.App{
  padding: 20px;
}

button{
  user-select: none;
}

.App .app-heading{
  width: min(1200px,90%);
  margin: 20px auto 30px;
  font-size: 20px;
}


.App .form-container{
  display: flex;
  flex-direction: column;
  row-gap: 20px;
  align-items: center;
  margin-bottom: 50px;
}

.App .form-container input,.App .form-container textarea{ 
  width: min(350px,90%);
  padding: 10px 20px;
  border: none;
  outline: 3px solid transparent;
  border-radius: 5px;
  box-shadow: 0 10px 15px -5px black;
}

.App .form-container textarea{ 
  transition: 0s;
  resize: vertical;
  min-height: 100px;
  max-height: 300px;
}

.App .form-container input:focus,.App .form-container textarea:focus{ 
    outline: 3px solid rgba(0, 213, 255, 0.964);

}

.App .form-container button{ 
  color: white;
  padding: 10px 35px;
  cursor: pointer;
  background-color: rgb(255, 0, 98);
  border: none;
  outline: none;
  font-weight: bold;
  transition: 0.071s;
  border-radius: 5px;
  box-shadow:  0 15px 15px -10px black;
}

.App .form-container button:hover{ 
  background-color: rgb(224, 2, 87);
  
}

.App .form-container button:active,.App .form-container button:active{ 
  background-color: rgb(184, 0, 70);
}


.App .question-element{
  background: rgb(234, 234, 234);
  margin: 0px auto 50px;
  width: min(1200px,90%);
  padding: 20px 30px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;
  transition: 0.2s ease;
  box-shadow: 0 20px 60px -10px black;
  color: black;
}

.App .question-element:hover{
  box-shadow: 0 20px 75px -20px rgb(0, 0, 0);

}

.App .question-title{
  font-weight: bold;
  font-size: 18px;
  margin-bottom: 10px;
}

.question-date {
  font-size: 14px;
  display: flex;
  align-items: center;
  gap: 5px;
  margin-bottom: 10px;
  flex-wrap: wrap;
color: rgb(109, 109, 109);

}

.question-date span{
  display: inline-block;
  width: 5px;
  aspect-ratio: 1;
  background-color: rgb(109, 109, 109);
  border-radius: 50%;
}

.view-answer-btn{
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 7px;
  font-size: 15px;
  color: rgb(2, 146, 151);
  margin-bottom: 25px;
  text-decoration: underline;
  user-select: none;
  width: fit-content;
  border-radius: 10px;
}

.view-answer-btn:hover{
  color: rgb(0, 74, 97);

}

.view-answer-dropdown-icon{
  transition: 0.5s ease;
}

.view-answer-dropdown-icon.active{
    transform: rotate(-180deg);
}

.App .question-element .btn-container{
  display: flex;
  gap: 20px;

}
.App .question-element button{
  padding: 10px 20px;
  cursor: pointer;
  background-color: rgb(10, 140, 145);
  color: white;
  border: none;
  outline: none;
  transition: 0.071s;
  border-radius: 10px;
  box-shadow:  0px 10px 15px -10px black;
}

.answer-container{
  margin-bottom: 20px;
}

.answer-element{
  background: white;
  box-shadow: 0px 20px 45px -15px rgb(0, 0, 0);
  margin: 10px 0;
  padding: 20px;
  margin-bottom: 30px;
  border-radius: 15px;
}

.answer-row-one{
  display: flex;
  justify-content: space-between;
  gap: 50px;
}

.answered-by{
  font-weight: 600;
  margin-bottom: 5px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 17px;
}

.answer-date{
font-size: 14px;
color: rgb(109, 109, 109);
display: flex;
align-items: center;
gap: 4px;
margin-bottom: 10px;
}

.answer-date span{
  display: inline-block;
  width: 5px;
  aspect-ratio: 1;
  background-color:rgb(109, 109, 109) ;
  border-radius: 50%;
}


.like-container{
  white-space: nowrap;
  display: flex;
  align-items: center;
  gap: 5px;
  user-select: none;
  font-size: 23px;
  cursor: pointer;
}

.like-container span{
  font-size: 16px;
}


.answer{
  text-indent: 20px;
  text-align: justify;
  padding: 20px;
  border-radius: 10px;
  background-color: rgb(234, 234, 234);
  font-size: 15px;
  
}

.upload-cancel-btn-container{
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
}

.cancel-btn{
  background: rgb(255, 81, 0) !important;
}

.form-container.answer-upload{
  margin-top: 40px;
  position: relative;
}

.form-container.answer-upload::before{
  position: absolute;
  content: "";
  background-color: rgb(198, 198, 198);
  height: 4px;
  width: 100%;
  top: -30px;
}

.form-container.answer-upload input, .form-container.answer-upload textarea{
  width: max(70%,250px);
  padding: 15px;
  border-radius: 0;
  box-shadow: 0 10px 13px -8px black;

  border-radius: 10px;
}

.null-question-data{
  text-align: center;
}

.asked-by-text{
  font-weight: bold;
  color: rgb(45, 45, 45);

}


.loader{
  width: 25px;
  aspect-ratio: 1;
  margin: 50px auto;
  border: 4px solid transparent;
  border-top-color: white;
  border-left-color: white;
  border-radius: 50%;
  animation: loader 1s linear infinite;

}

@keyframes loader{
  from {
    transform: rotate(0);
  }
  to{
    transform: rotate(360deg);
  }
  
}